.header {
  background-color: var(--white);
  z-index: 999;
  position: relative;
}

.header--navMask {
  z-index: 998;
  visibility: collapse;
  opacity: 0;
  background-image: linear-gradient(#2d314d 0%, #2d314d00 100%);
  width: 100%;
  height: 100%;
  transition: all .4s cubic-bezier(.445, .05, .55, .95);
  position: fixed;
  top: 0;
  left: 0;
}

.header--navMask__open {
  visibility: visible;
  opacity: 1;
}

.header .content {
  max-width: var(--section-width);
  padding: 0 var(--section-padding);
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 64px;
  margin: auto;
  display: flex;
}

.header--navigation {
  left: var(--section-padding);
  right: var(--section-padding);
  top: calc(64px + var(--section-padding));
  width: calc(100% - 2 * var(--section-padding));
  background-color: var(--white);
  transition: var(--transition-4);
  visibility: collapse;
  opacity: 0;
  border-radius: 4px;
  padding: 2rem;
  position: absolute;
  transform: translateY(100%);
}

.header--navigation__open {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

.header--navigation ul {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
  list-style-type: none;
  display: flex;
}

.header--navigation a {
  letter-spacing: -.00875rem;
  font-size: 1.125rem;
  line-height: 1.125rem;
  font-weight: var(--fw-medium);
  color: var(--dark);
  transition: var(--transition-1);
  text-decoration: none;
}

.header--navigation--indicator, .header .requestInviteCta {
  display: none;
}

.header--openMenu {
  cursor: pointer;
  background-color: #0000;
  border: none;
  place-items: center;
  width: 24px;
  height: 24px;
  display: grid;
}

.hamburgerIcon {
  width: 100%;
  height: 11px;
  position: relative;
}

.hamburgerIcon div {
  background-color: var(--dark);
  width: 100%;
  height: 1px;
  position: absolute;
  top: 50%;
  left: 0;
  translate: 0 -50%;
}

.hamburgerIcon--top {
  transition: var(--transition-3);
  transform-origin: center;
  transform: translateY(-4px);
}

.hamburgerIcon--middle {
  transform-origin: 0;
  transition: var(--transition-3);
}

.hamburgerIcon--bottom {
  transition: var(--transition-3);
  transform-origin: center;
  transform: translateY(4px);
}

.header--openMenu__open .hamburgerIcon--top {
  transform: rotate(45deg)translateY(0);
}

.header--openMenu__open .hamburgerIcon--middle {
  opacity: 0;
  transform: scaleX(0);
}

.header--openMenu__open .hamburgerIcon--bottom {
  transform: rotate(-45deg)translateY(0);
}

@media (hover: hover) and (pointer: fine) {
  .header--navigation a:hover {
    color: var(--dark);
  }
}

@media screen and (width >= 1100px) {
  .header .content {
    height: 80px;
  }

  .header--navMask {
    display: none;
  }

  .header--navigation {
    left: unset;
    top: unset;
    width: unset;
    background-color: unset;
    border-radius: unset;
    padding: unset;
    visibility: visible;
    opacity: 1;
    place-items: center;
    height: 100%;
    transition: unset;
    display: grid;
    position: relative;
    transform: translateY(0);
  }

  .header--navigation ul {
    flex-direction: row;
  }

  .header--navigation:hover .header--navigation--indicator {
    opacity: 1;
  }

  .header--navigation--indicator {
    background-image: var(--gradient);
    transform-origin: 0;
    opacity: 0;
    width: 51px;
    height: 4px;
    transition: all .25s cubic-bezier(.165, .84, .44, 1);
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    transform: translateX(125.217px);
  }

  .header--navigation a {
    letter-spacing: -.006875rem;
    color: var(--greyish--blue);
    font-size: .875rem;
    line-height: .875rem;
  }

  .header--openMenu {
    display: none;
  }

  .header .requestInviteCta {
    display: block;
  }
}
